import React from "react";
import NavigationButtons from "./NavigationButtons";
import Typewriter from "./Typewriter";
import AnimateHeight from "react-animate-height";

const loremTexts = [
	`Welcome to my porfolio.
\\n
I'm a **fullstack** developer 
who loves **programming**
with a game developer background.`,
	`* Advanced English
* JS Frameworks & Libraries (React, NodeJS, Express, React-Native, Axios)
* PHP Frameworks (Laravel, CodeIgniter)
* CSS Frameworks & Libraries (Bootstrap, Primer, SCSS, LESS & Others)`,
	`
*Email*
*Github*
*Linkedin*`,
];
const mainTexts = ["Willian Matheus Carminatti", "Skills", "Contact"];

export default class PageController extends React.Component<any, {}> {
	render() {
		return (
			<>
				<div className="info">
					<div className="responsive-content">
						<div className="avatar">
							<img src="/static/me.jpeg" alt="" />
						</div>
						<div className="name">
							<Typewriter height={56} texts={mainTexts} currentIndex={this.props.currentIndex} show />
						</div>
					</div>
					<div className="about">
						<Typewriter height={17} texts={loremTexts} currentIndex={this.props.currentIndex} show />
					</div>
				</div>
			</>
		);
	}
}
