import React from "react";

export default class NavigationButtons extends React.Component<any, {}> {
	render() {
		return (
			<div className="buttons">
				<div className="button" onClick={() => this.props.onChange(0)}>
					About
				</div>
				<div className="button" onClick={() => this.props.onChange(1)}>
					Skills
				</div>
				<div className="button" onClick={() => this.props.onChange(2)}>
					Contact
				</div>
			</div>
		);
	}
}
