import hljs from "highlight.js";
import React from "react";
import AnimateHeight from "react-animate-height";
import ReactMarkdown from "react-markdown";

interface ITypewriterProps {
	texts: string[];
	onChange?: Function;
	currentIndex: number;
	show?: boolean;
	height?: number;
}

const delay = (ms: number) => new Promise((res) => setTimeout(res, ms));

const unicodeSplit = "¶";
const delimiter = "\n";

class Writer extends React.Component<any, {}> {
	public state = {
		text: "",
		nextText: "",
		currentChar: 0,
		deleting: false,
	};

	public interval = null as any;
	componentDidMount() {
		if (this.props.text.length > 0) {
			this.setState({ nextText: this.props.text });
		}
		
		this.interval = setInterval(this.typeWrite, 30);
	}
	componentDidUpdate(prevProps: any, prevState: any) {
		if (prevProps.text !== this.props.text) {
			this.setState({ nextText: this.props.text,deleting: false });
		}
		if (this.state.deleting === true && prevState.deleting === false) {
			setTimeout(() => {
				if (this.props.onDelete) {
					this.props.onDelete();
				}
			}, 1000);
		}
	}
	componentWillUnmount() {
		clearInterval(this.interval);
	}

	typeWrite = () => {
		if (this.state.nextText.length > 0 && this.state.text !== this.state.nextText) {
			let text = this.state.text;
			let currentChar = text.length === 0 ? this.state.currentChar : text.length - 1;

			if (text[currentChar] == undefined) {
				text += this.state.nextText[currentChar];
			} else {
				if (this.state.nextText.startsWith(text) == false) {
					text = text.slice(0, text.length - 1);
				} else {
					currentChar++;
					text += this.state.nextText[currentChar];
				}
			}

			if (this.state.nextText === unicodeSplit && text == unicodeSplit) {
				this.setState({
					deleting: true,
				});
			}
			if (this.props.onChange) {
				this.props.onChange();
			}
			this.setState({ text, currentChar });
		}
	};

	render() {
		// if (this.state.text === unicodeSplit) {
		// 	return null;
		// }
		if (this.props.show === true) {
			return (
				<AnimateHeight height={this.state.deleting ? 0: (this.props.height ?? 17)} duration={300}>
				<ReactMarkdown>
					{this.state.text
						.split("\\n")
						.join("&nbsp;")
						.split(unicodeSplit)
						.join("")
						.split("Email")
						.join("[Email](mailto:willian_ctt@outlook.com)")
						.split("Github")
						.join("[Github](https://github.com/willian-ctt)")
						.split("Linkedin")
						.join("[LinkedIn](https://www.linkedin.com/in/willian-carminatti-57123b15a/)")}
				</ReactMarkdown>
				</AnimateHeight>
			);
		}
		return <>{this.state.text + "\n"}</>;
	}
}

export default class Typewriter extends React.Component<ITypewriterProps, {}> {
	public state = {
		currentIndex: this.props.currentIndex,
		linedTexts: [] as string[][],
		activeText: [] as string[],
		activeComponents: [] as React.ReactElement[],
		renderedTexts: "",
	};
	componentDidMount() {
		let linedTexts = this.props.texts.map((item) => item.split(delimiter));
		// let targetText = linedTexts[this.state.currentIndex][this.state.currentLine];
		this.setState(
			{
				activeText: linedTexts[this.state.currentIndex],
				linedTexts,
			},
			() => {
				this.setState({
					activeComponents: this.renderContent(),
				});
			}
		);

		// hljs.highlight(this.ref.current);
	}

	componentDidUpdate(prevProps: any, prevState: any) {
		if (prevProps.currentIndex !== this.props.currentIndex) {
			let linedTexts = this.props.texts.map((item) => item.split(delimiter));
			this.setState({
				currentIndex: this.props.currentIndex,
				activeText: linedTexts[this.props.currentIndex],
			});
		}
		if (prevState.currentIndex !== this.state.currentIndex) {
			this.setState({
				activeComponents: this.renderContent(),
			});
		}
	}

	Hightlight = () => {
		if (this.props.show !== true) {
			let output = hljs.highlight(this.ref.current.innerText, {
				language: "tsx",
			}).value;
			if (this.props.onChange) {
				this.props.onChange(output);
			}
		}
		// console.log("C",output);
	};

	renderContent = () => {
		var output = [...this.state.activeComponents];
		for (var i in this.state.activeText) {
			const activeText = this.state.activeText[i];
			output[i] = <Writer height={this.props.height} show={this.props.show} key={i} text={activeText} onChange={this.Hightlight} />;
		}
		for (var i in output) {
			if (this.state.activeText[i]?.length <= 0 || this.state.activeText[i] === undefined) {
				output[i] = <Writer height={this.props.height} show={this.props.show} key={i} text={unicodeSplit} onChange={this.Hightlight} />;
			}
		}

		return output;
	};

	public ref;
	constructor(props: any) {
		super(props);
		this.ref = React.createRef<any>();
	}

	render() {
		if (this.props.show === true) {
			return this.state.activeComponents;
		}
		return (
			<>
				{/* <button
					onClick={() => {
						let currentIndex = this.state.currentIndex;
						if (this.state.linedTexts[this.state.currentIndex + 1]) {
							currentIndex++;
						} else {
							currentIndex = 0;
						}
						this.setState({
							currentIndex: currentIndex,
							activeText: this.state.linedTexts[currentIndex],
						});
					}}>
					chagne
				</button> */}
				<pre style={{ display: "none" }}>
					<code className="language-tsx" ref={this.ref}>
						{this.state.activeComponents}
					</code>
				</pre>
			</>
		);
		return (
			<>
				<div>{this.state.activeComponents}</div>
				{this.state.renderedTexts}

				{/* {this.state.linedTexts?.map((item, key) => {
					return <Writer key={key}>{this.state.linedTexts[this.state.currentIndex]}</Writer>;
				})} */}
			</>
		);
	}
}
