export default class HomePage extends React.Component<any, {}> {
	public state = {
		texts: "",
		currentIndex: 0,
	};

	getContent = () => {
		let output = [];
		let splitted = this.state.texts.split("\n");
		for (let i = 0; i < splitted.length; i++) {
			output.push("<i class='count' data-line-number='" + (i + 1) + "'></i>" + splitted[i]);
		}
		// console.log(this.state.texts);
		return output.join("\n");
	};

	render() {
		return (
			// <pre >
			<>
				<pre>
					<code className="language-tsx hljs" dangerouslySetInnerHTML={{ __html: this.getContent() }}></code>
				</pre>
				<div className="navigation">
					<PageController
						currentIndex={this.state.currentIndex}
						onChange={(currentIndex: number) => {
							this.setState({ currentIndex });
						}}
					/>
					<NavigationButtons
						onChange={(currentIndex: number) => {
							this.setState({ currentIndex });
						}}
					/>
				</div>
				<Typewriter
					currentIndex={this.state.currentIndex}
					texts={texts}
					onChange={(texts: any) => this.setState({ texts: texts.split("\t").join("  ") })}
				/>
				{/* // 	</code> */}
			</>
			// </pre>
		);
	}
}
